import { computed, defineComponent } from '@nuxtjs/composition-api';
export default defineComponent({
    props: {
        currentOnboardingStep: {
            type: Number,
            required: true
        },
        onboardingSteps: {
            type: Array,
            required: true
        }
    },
    setup(props) {
        const step = computed(() => props.currentOnboardingStep);
        return { step };
    }
});
